import React, { Component } from 'react'
import {NavLink} from "react-router-dom";
export default class Nav extends Component {
  render() {
    return (
        <div>
        <nav className="navbar navbar-expand-lg navbar-light fixed-top" id="mainNav">
        <div className="container">
          <a className="navbar-brand js-scroll-trigger" href="#page-top">Start Bootstrap</a>
          <button className="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
            Menu
            <i className="fas fa-bars" />
          </button>
          <div className="collapse navbar-collapse" id="navbarResponsive">
            <ul className="navbar-nav ml-auto">
    
              <li className="nav-item">
                <NavLink to="/" className="nav-link js-scroll-trigger" aria-current="true" >Trang chủ</NavLink>
            </li>
            <li className="nav-item">
                <NavLink to="/tin" className="nav-link js-scroll-trigger" activeClassName="selected_menu" aria-current="true">Bài viết</NavLink>
            </li>
           
            <li className="nav-item">
                <NavLink to="/lien-he" className="nav-link js-scroll-trigger" aria-current="true" activeClassName="selected_menu">Liên hệ</NavLink>
            </li>
            </ul>
          </div>
        </div>
      </nav>
      </div>
    )
  }
}
