import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isRedirect: false,
      Ngay: "thu 4"
    }
  }

  isChange = (event) => {
    const ten = event.target.name;
    const giatri = event.target.value;
    this.setState({
      [ten]: giatri
    })
  }
  isChangeFile = (event) => {
    const anh = event.target.files[0].name;
    this.setState({
      fAnh: anh
    })
  }
  submitForm = (event) => {
    event.preventDefault();
    this.setState({
      isRedirect: true
    });

  }

  getGiaTri = () => {
    var noiDung = "";
    noiDung += "Ten nhan duoc la:" + this.state.fName;
    noiDung += "/Email la:" + this.state.fEmail;
    noiDung += "/SDT la:" + this.state.fPhone;
    noiDung += "/Message la:" + this.state.fMess;
    noiDung += "/Ngày la:" + this.state.Ngay;
    noiDung += "/Tên ảnh la:" + this.state.fAnh;
    return noiDung;
  }
    render() {
      
      if(this.state.isRedirect === true){
        console.log(this.getGiaTri());
        
        return <Redirect to="/" />;
      }
        return (
            <div>
                <header className="masthead">
          <div className="container h-100">
            <div className="row h-100">
              <div className="col-lg-7 my-auto">
                <div className="header-content mx-auto">
                  <h1>Liên hệ</h1>
                </div>
              </div>
            </div>
          </div>
        </header>
        <section className="page-section" id="contact">
          <div className="container">
            {/* Contact Section Heading */}
            <h2 className="page-section-heading text-center text-uppercase text-secondary mb-0">Liên hệ</h2>
            {/* Icon Divider */}
            <div className="divider-custom">
              <div className="divider-custom-line" />
              <div className="divider-custom-icon text-center">
                <i className="fas fa-star" />
              </div>
              <div className="divider-custom-line" />
            </div>
            {/* Contact Section Form */}
            <div className="row">
              <div className="col-lg-8 mx-auto">
                {/* To configure the contact form email address, go to mail/contact_me.php and update the email address in the PHP file on line 19. */}
                <form name="sentMessage" id="contactForm" noValidate="novalidate">
                  <div className="control-group">
                    <div className="form-group floating-label-form-group controls mb-0 pb-2">
                      <label>Name</label>
                      <input className="form-control" name="fName" onChange={(event) => this.isChange(event)} id="name" type="text" placeholder="Name" required="required" data-validation-required-message="Please enter your name." />
                      <p className="help-block text-danger" />
                    </div>
                  </div>
                  <div className="control-group">
                    <div className="form-group floating-label-form-group controls mb-0 pb-2">
                      <label>Email Address</label>
                      <input className="form-control" id="email" onChange={(event) => this.isChange(event)} name="fEmail" type="email" placeholder="Email Address" required="required" data-validation-required-message="Please enter your email address." aria-invalid="false" />
                      <p className="help-block text-danger" />
                    </div>
                  </div>
                  <div className="control-group">
                    <div className="form-group floating-label-form-group controls mb-0 pb-2">
                      <label>Phone Number</label>
                      <input className="form-control" id="phone" onChange={(event) => this.isChange(event)} name="fPhone" type="tel" placeholder="Phone Number" required="required" data-validation-required-message="Please enter your phone number." />
                      <p className="help-block text-danger" />
                    </div>
                  </div>
                  <div className="control-group">
                    <div className="form-group floating-label-form-group controls mb-0 pb-2">
                      <label>Message</label>
                      <textarea className="form-control" onChange={(event) => this.isChange(event)} name="fMess" id="message" rows={5} placeholder="Message" required="required" data-validation-required-message="Please enter a message." defaultValue={""} />
                      <p className="help-block text-danger" />
                    </div>
                  </div>
                   <div className="control-group">
                    <div className="form-group floating-label-form-group controls mb-0 pb-2">
                      <label>Chọn Ngày</label>

                        <select value={this.state.Ngay} className="form-control" name="fNgay" onChange={(event) => this.isChange(event)}>
                          <option value="thu 2">Ngày thứ 2</option>
                          <option value="thu 3">Ngày thứ 3</option>
                          <option value="thu 4">Ngày thứ 4</option>
                        </select>
                     </div>
                  </div>
                  <div className="form-group">
                    <label>Ảnh</label>
                    <input type="file" onChange={(event) => this.isChangeFile(event)} className="form-control-file" name="fAnh"/>
                  
                  
                  </div>
                  <br />
                  <div id="success" />
                  <div className="form-group">
                    <button type="submit" onClick={(event) => this.submitForm(event)} className="btn btn-primary btn-xl" id="sendMessageButton">Send</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </section>
            </div>
        );
    }
}
export default Contact;